/* #region page*/
.page {
  font-family: "Manrope", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  color: #1B2129;
  font-size: 16px;
  line-height: 22.4px;
  scroll-behavior: smooth;

}

b.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;

  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

/* #endregion*/

/* #region header*/
.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.header__title {
  margin: 0;
  font-family: "Manrope", serif;
  font-weight: 700;
  color: #FFFFFF;
  font-size: 36px;
  line-height: 36px;

}.header__bottom {
  padding: 0 20px 40px;
}
/* #endregion*/

/* #region menu */

.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  display: flex;
  gap: 24px;
}

.top-bar__logo {
  display: flex;
}

.menu {
  box-sizing: border-box;
  font-weight: 700;
  height: 100vh;
  background-color: #F9F9F9;
  text-transform: uppercase;
}

.menu__top {
  margin-bottom: 32px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__phone-number {
  display: block;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
  color: inherit;
  text-decoration: none;
}
.menu__call-to-order {
  display: block;
  width: fit-content;
  padding-bottom: 7px;
  border-bottom: 1px solid #1B2129;

  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  color: inherit;
  text-decoration: none;
}
/* #endregion */

/* #region icon */
.icon {
  display: block;
  height: 24px;
  width: 24px;
}

.icon--phone {
  background-image: url(../images/icons/Phone-number.png);
}

.icon--menu {
  background-image: url(../images/icons/Icon-Burger-menu.png);
}

.icon--close {
  background-image: url(../images/icons/Icon-Close.png);
}


/* #endregion*/
.nav__link {
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

/* #region footer*/
.footer {
  display: flex;
}
/* #endregion*/
